// Learn life-cycle callbacks:
//  - [Chinese] http://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        setNode:cc.Node,

    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var self = this
        this.initAllBtn()
        this.initSlider()
        this.initcheckBox()
        var set_bg = this.node.getChildByName("set_bg")//cc.find('Canvas/set_layer/set_bg');
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,set_bg,callback);
        this.refreshVolume()

    },

    start () {
    },
    //active = true
    onEnable(){
        this.refreshVolume()
    },
    refreshVolume:function(){
        var _sliderMusic = this.setNode.getChildByName("slider_music")
        var _sliderSound = this.setNode.getChildByName("slider_sound")

        var musicMask = _sliderMusic.getChildByName("mask_node")
        var soundMask = _sliderSound.getChildByName("mask_node")
        var sliderMusic = _sliderMusic.getComponent(cc.Slider)
        var sliderSound = _sliderSound.getComponent(cc.Slider)
        musicMask.width = 500 * wsft.audioFuncs.musicVolume;
        soundMask.width = 500* wsft.audioFuncs.soundVolume;
        sliderMusic.progress = wsft.audioFuncs.musicVolume;
        sliderSound.progress = wsft.audioFuncs.soundVolume;
    },
    //_checkBox
    checkBoxCallback:function(toggle, name){
        console.log(toggle)
        if(name == "toggle"){
            if(toggle.isChecked){ //cc.Color(255, 255, 73)
                wsft.uiFuncs.showTip('暂时未开通GPS功能！',30,2,(new cc.Color(0, 0, 0)))
            }else{
                wsft.uiFuncs.showTip('暂时未开通GPS功能',30,2,(new cc.Color(0, 0, 0)))
            }
        }
    },
    initcheckBox:function(){
        var self = this 
        var checkBoxhander = function(slider,_name){
            // var slider = cc.find(_sliderPath)
            wsft.event.addCheckBoxEvent(slider,self.node,'setLayer','checkBoxCallback',_name)
        }
        checkBoxhander(this.node.getChildByName("distance_toggle"),"toggle")
    },
    //btn
    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "close"){
            this.node.active = false;
        }
        else if(name == 'change'){
            cc.director.loadScene('LoginScene')
        }
        else{
            
        }

    },
    initAllBtn:function(){
        var self = this
        var btnHader = function(btn,name){
            // var btn = cc.find(btnPath);
            wsft.event.addBtnEvent(btn,self.node,'setLayer','allBtnCallback',name);
        }
        btnHader(this.node.getChildByName("close_btn"),'close'); //close
        btnHader(this.node.getChildByName("change_account_btn"),'change'); //更换账号
    },
    //slider
    sliderCallback:function(slider,name){
        // console.log(slider)
        console.log(name)
        if (name == "music") {
            // this._musicMask.width = this._musicWidth * slider.progress;
            wsft.audioFuncs.setMusicVolume(slider.progress)
        } else if(name == "sound") {
            wsft.audioFuncs.setSoundVolume(slider.progress)
            // this._soundMask.width = this._soundWidth * slider.progress;
        }
        this.refreshVolume()
    },
    initSlider:function(){
        var self = this 
        var sliderhander = function(slider,_name){
            // var slider = cc.find(_sliderPath)
            wsft.event.addSliderEvent(slider,self.node,'setLayer','sliderCallback',_name)
        }
        sliderhander(this.setNode.getChildByName("slider_music"),"music")
        sliderhander(this.setNode.getChildByName("slider_sound"),"sound")
    },
    onDisable(){
        
    },

    // update (dt) {},
});
