
var _copy = require("copy_h5")
var _copyStr = "chifengmajiang"
cc.Class({
    extends: cc.Component,

    properties: {
        
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad () {
        var self = this
        this.initAllBtn()
        var shop_bg = this.node.getChildByName("shop_bg");
        var callback = function(){
            self.node.active = false;
        }
        wsft.uiFuncs.bindTouchLayer(this.node,shop_bg,callback);
    },

    start () {
        var wxLabel = this.node.getChildByName("shop_lable_3").getComponent(cc.Label)
        wxLabel.string = _copyStr
    },

    allBtnCallback:function(event,name){
        cc.log(name)
        if(name === "close" || name == "sure"){
            this.node.active = false;
        }
        else if(name == 'copy'){
            if(cc.sys.isBrowser){//判断是否是浏览器
                if(_copy(_copyStr)){
                    wsft.uiFuncs.showTip('复制成功',30)
                }else{
                    wsft.uiFuncs.showTip('复制失败',30)
                }
            }else{
                

            }
        }
        else{
            
        }

    },
    initAllBtn:function(){
        var self = this
        var btnHader = function(btn,name){
            // var btn = cc.find(btnPath);
            wsft.event.addBtnEvent(btn,self.node,'shopLayer','allBtnCallback',name);
        }
        btnHader(this.node.getChildByName("close_btn"),'close'); //close
        btnHader(this.node.getChildByName("copy_btn"),'copy'); //copy
        btnHader(this.node.getChildByName("sure_btn"),'sure'); //sure


    },
    // update (dt) {},
});
